































import service from '../../service.js';
import {
	mapMutations
} from 'vuex' // 把状态和方法都引进来
export default {
	data() {
		return {
			account: '', // 用户名
			password: '',
			height: '',
			avatar: '', // 头像
			info: {}
		}
	},
	mounted() {
		this.getHeight();
	},
	methods: {
		...mapMutations(['login']), // 这里提交index.js中store仓库的mutations的Login方法
		wxLogin() { // 微信登录
			let _this = this;
			uni.showLoading({
				title:'正在努力登录...'
			});
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							uni.hideLoading();
							_this.info.name = infoRes.userInfo.nickName;
							_this.info.avatar = infoRes.userInfo.avatarUrl;
							_this.toMain(_this.info); // 把用户名传给方法toMain
						}
					});
				}
			});
		},
		bindLogin() {
			/**
			 * 客户端对账号信息进行一些必要的校验。
			 * 实际开发中，根据业务需要进行处理，这里仅做示例。
			 */
			let nameLen = this.account.length;
			let passwordLen = this.password.length;
			if(nameLen == 0){
				uni.showToast({
					icon: 'none',
					title: '请输入用户名'
				});
				return;
			}else if (nameLen < 5) {
				uni.showToast({
					icon: 'none',
					title: '用户名最短为 5 个字符'
				});
				return;
			}
			if(passwordLen == 0){
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				});
				return;
			}else if (passwordLen < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			/**
			 * 下面简单模拟下服务端的处理
			 * 检测用户账号密码是否在已注册的用户列表中
			 * 实际开发中，使用 uni.request 将账号信息发送至服务端，客户端在回调函数中获取结果信息。
			 */
			const data = {
				account: this.account,
				password: this.password
			};
			// some方法是用来方法内的内容，返回结果是否为true
			const validUser = service.getUsers().some(function(user) {
				return data.account === user.account && data.password === user.password;
			});
			if (validUser) {
				this.toMain(this.account); // 把用户名传给方法toMain
			} else {
				uni.showToast({
					icon: 'none',
					title: '用户名或密码不正确',
				});
			}
		},
		toMain(userName) {
			if (userName) {
				this.login(userName); // 触发 mutations的login方法
				uni.showToast({
					title:'登录成功！',
					duration:1000
				})
				uni.navigateBack(); // 登录成功，返回首页
			}
		},
		goRegister() {
			uni.navigateTo({
				url: '../reg/reg'
			})
		},
		getHeight() {
			uni.getSystemInfo({
				success: (res) => {
					this.height = res.screenHeight; // 屏幕高度,当页面需要到状态栏顶部时，高度需要使用屏幕高度，如果需要导航栏存在，高度获取手机可用高度ScreenHeight
				}
			})
		}
	}
}



